import { Node, Graph } from '../Graph';

export const getPath = (graph: Graph, p0: Node, p1: Node) => {

    const nodes = []
    for(let i = 0 ; i < graph.nodes.length; ++i) {
        const node =graph.nodes[i];
        const newNode = new NodeAStar(node.vec.x, node.vec.y, node.vec.z)
        newNode.id = node.id;
        nodes.push(newNode);
    }

    const edges = []
    for(let i = 0 ; i < graph.edges.length; ++i) {
        const edge =graph.edges[i];
        const newEdge = new EdgeAStar( nodes[edge.n0.id], nodes[edge.n1.id]  )
        edges.push(newEdge);
    }
    return AStart.gePath(nodes[p0.id], nodes[p1.id])
}


class AStart {
    public static TYPE = {
        Manhattan: 'Manhattan  ',
        Euclidean: 'Euclidean ',
    }
    public static strength = 0.01;
    public static distanceManhattan = (x0: number, y0: number, z0: number, x1: number, y1: number, z1: number) => {
        return Math.abs(x0 - x1) + Math.abs(y0 - y1) + Math.abs(z0 - z1);
    }
    public static distanceEuclidean = (x0: number, y0: number, z0: number, x1: number, y1: number, z1: number) => {
        return Math.sqrt((x0 - x1) * (x0 - x1) + (y0 - y1) * (y0 - y1) + (z0 - z1) * (z0 - z1));
    }
    public static gePath = (startNode: NodeAStar, endNode, type?: string) => {
        this.typeHeuristic = this.distanceEuclidean;
        if (type === AStart.TYPE.Manhattan) {
            this.typeHeuristic = this.distanceManhattan;
        }
        
        const openNodes = [startNode];
        const closedNodes: NodeAStar[] = [];
        let result = undefined;
        let count = 100;
        while (count--) {
            result = this.propagation(openNodes, closedNodes, endNode);
            if (result !== undefined && result.length) {
                return result;
            }
        }
        return result;
    }

    private static path: NodeAStar[] = [];
    private static propagation = (openNodes, closedNodes, endNode) => {
        if (openNodes.length > 0) {
            // console.log(openNodes)
            //find the lowest f in open nodes
            let lowestF = 0;
            for (let i = 0; i < openNodes.length; i++) {
                if (openNodes[i].f < openNodes[lowestF].f) {
                    lowestF = i;
                }

                if (openNodes[i].f === openNodes[lowestF].f) {
                    if (openNodes[i].g > openNodes[lowestF].g) {
                        lowestF = i;
                    }
                }
            }
            let current: NodeAStar = openNodes[lowestF];

            if (current === endNode) {
                //reached the end node so wait a sec and restart
                return [current, current.previous, ...this.path];
            }

            //calculate path
            this.path = [];
            let temp = current;
            while (temp.previous) {
                this.path.push(temp.previous);
                temp = temp.previous;

                if (temp.previous === this.path[this.path.length-2]) {
                    break;
                }
            }
            // console.log(this.path.length);

            openNodes.splice(openNodes.indexOf(current), 1);
            closedNodes.push(current);

            let neighbors = current.neighbors;
            for (let i = 0; i < neighbors.length; i++) {
                let neighbor = neighbors[i];

                if (!closedNodes.includes(neighbor) && !neighbor.blocked) {
                    let tempG = current.g + this.heuristic(neighbor, current) * this.strength;

                    if (!openNodes.includes(neighbor)) {
                        openNodes.push(neighbor);
                    }
                    else {
                        continue;
                    }

                    neighbor.g = tempG;
                    neighbor.h = this.heuristic(neighbor, endNode);
                    neighbor.f = neighbor.g + neighbor.h;
                    neighbor.previous = current
                }
            }
            return undefined;
        } else {
            return this.path;
        }
    }
    private static typeHeuristic: (x0: number, y0: number, z0: number, x1: number, y1: number, z1: number) => number;
    private static heuristic = (a: NodeAStar, b: NodeAStar) => {
        return this.typeHeuristic(a.x, a.y, a.z, b.x, b.y, b.z);
    }

}
class NodeAStar {
    public id = -1;

    public f = 0;
    public h = 0;

    public g = 0;

    public x = 0;
    public y = 0;
    public z = 0;
    public neighbors: NodeAStar[] = [];
    public edges: EdgeAStar[] = [];
    public previous = undefined;
    public blocked = false;
    constructor(x: number, y: number, z: number) {
        this.x = x;
        this.y = y;
        this.z = z;
    }
}
class EdgeAStar {
    public id: number = -1;
    public n0: NodeAStar;
    public n1: NodeAStar;

    constructor(n0: NodeAStar, n1: NodeAStar) {
        this.n0 = n0;
        this.n1 = n1;
        this.n0.edges.push(this);
        this.n1.edges.push(this);
        n0.neighbors.push(n1);
        n1.neighbors.push(n0);
    }
}

const buildNetwork = (lns: any) => {
    //  console.log(lns);

    let nodes: NodeAStar[] = []
    let edges: EdgeAStar[] = []

    let nodesForEdge: NodeAStar[] = [];
    for (let i = 0; i < lns.length; ++i) {
        let p0 = lns[i].p0;
        let p1 = lns[i].p1;
        nodesForEdge.push(AddNode(p0, nodes));
        nodesForEdge.push(AddNode(p1, nodes));
    }
    for (let i = 0; i < nodesForEdge.length; i += 2) {
        AddEdge(nodesForEdge[i], nodesForEdge[i + 1], edges);
    }

    return nodes
}

const AddEdge = (n1: NodeAStar, n2: NodeAStar, edges: EdgeAStar[]): EdgeAStar | undefined => {
    if (n1 === n2) return undefined;

    const testedEdge = findEdge(n1, n2, edges);
    if (testedEdge !== undefined) {
        return testedEdge;
    }

    let edge = new EdgeAStar(n1, n2);
    edge.id = edges.length;
    edges.push(edge);
    return edge;
}
const findEdge = (n0: NodeAStar, n1: NodeAStar, edges: EdgeAStar[]): EdgeAStar | undefined => {
    for (let i = 0; i < edges.length; ++i) {
        if ((edges[i].n0 === n0 && edges[i].n1 === n1) || (edges[i].n0 === n1 && edges[i].n1 === n0)) {
            return edges[i];
        }
    }
    return undefined;
}
const AddNode = (p: any, nodes: NodeAStar[]): NodeAStar => {
    for (let i = 0; i < nodes.length; ++i) {
        if (distanceTo(p.x, p.y, p.z, nodes[i].x, nodes[i].y, nodes[i].z) < 0.01) {
            return nodes[i];
        }
    }

    let node = new NodeAStar(p.x, p.y, p.z);
    node.id = nodes.length;
    nodes.push(node);
    return node;
}


const distanceTo = (x0: number, y0: number, z0: number, x1: number, y1: number, z1: number): number => {
    return Math.sqrt((x0 - x1) * (x0 - x1) + (y0 - y1) * (y0 - y1) + (z0 - z1) * (z0 - z1));
}