import { Graph, Node, Edge } from './Graph';

import { getBFS, getDFS } from './Algorithm/Search';
import { getPath } from './Algorithm/Dijkstra';
import { getPath as getPathAStar } from './Algorithm/AStar';
import { getMST } from './Algorithm/MST';

import {
    Vector3, distance,
    getLineIntersectPlane, pointOnLine,
} from './Graph/util';

export { Node, Edge } from './Graph';
// import { Solution } from '../..';

export class GraphSystem {
    public graph: Graph;
    public pathMST = undefined;

    public nodeStart: Node | undefined = undefined;
    public nodeEnd: Node | undefined = undefined;

    public nodePre: Node | Edge | undefined = undefined;

    private _captured: Node | Edge | undefined = undefined;

    constructor() {
        this.graph = new Graph(this);

    }
    public get captured() {
        return this._captured
    }
    public set captured(v) {
        if (this._captured) {
            this._captured.isSelected = false;
        }
        this._captured = v;
        if (v) {
            this._captured.isSelected = true;
        }
    }
    public resetCapture() {
        this.captured = undefined;
        this.nodePre = undefined;
    }
    // ..............................................................
    public addNode(x: number, y: number, z: number = 0) {
        const node = new Node(this.graph, x, y, 0);
        this.graph.nodes.push(node);
        this.resetCapture();
        return node;
    }
    public addEdge(n0: Node, n1: Node) {
        if (n0 === n1) return undefined;
        if (this.findEdgeByNodes(n0, n1)) return undefined;

        const edge = new Edge(n0, n1);
        n0.edges.push(edge);
        n1.edges.push(edge);
        this.graph.edges.push(edge);

        this.edgeSplitByEdge(edge);

        this.resetCapture();
        return edge;
    }
    public addEdgeByClosestNode(node: Node) {
        const nn = this.findNode(node);
        if (nn) this.addEdge(node, nn);
        this.resetCapture();
    }
    public addEdgeSplit(n0: Node, n1: Node) {
        const edge = new Edge(n0, n1);
        n0.edges.push(edge);
        n1.edges.push(edge);
        this.graph.edges.push(edge);
        return edge;
    }
    // ..............................................................
    public edgeSplitByNode(node: Node, edge: Edge) {
        const n0 = edge.n0;
        const n1 = edge.n1;

        this.addEdge(n0, node);
        this.addEdge(node, n1);

        this.removeEdge(edge);
        this.resetCapture();
    }
    // edge split from a moved node by it's edges
    public edgeSplitByNodeEdges(node: Node) {
        const edgeRemove = [];

        let theEdgeLength = node.edges.length;
        for (let j = 0; j < theEdgeLength; ++j) {
            // console.log(j);

            const edge = node.edges[j];
            const nodes: Node[] = [];

            for (let i = this.graph.edges.length - 1; i > -1; --i) {
                const other = this.graph.edges[i];
                if (edge.n0 !== other.n0 &&
                    edge.n0 !== other.n1 &&
                    edge.n1 !== other.n0 &&
                    edge.n1 !== other.n1) {
                    const inter = getIntersectEdge(edge, other);
                    if (inter) {
                        // console.log(inter)
                        const nn = new Node(this.graph, inter[0], inter[1])
                        this.graph.nodes.push(nn);
                        this.addEdgeSplit(nn, other.n0);
                        this.addEdgeSplit(nn, other.n1);

                        this.removeEdge(other);
                        nodes.push(nn);
                    }
                }
            }

            if (nodes.length > 0) {
                let nodeFirst = edge.n0;
                nodes.push(edge.n1);
                const disList = nodes.map((n) => { return distance(n.vec, edge.n0.vec); });
                const indices = disList.map((val, index) => index);
                const sortedDis = indices.sort((a, b) => disList[a] - disList[b]);
                this.addEdgeSplit(nodeFirst, nodes[sortedDis[0]]);
                for (let i = 0; i < sortedDis.length - 1; ++i) {
                    this.addEdgeSplit(nodes[sortedDis[i]], nodes[sortedDis[i + 1]]);
                }

                // edge.n0.removeEdge(edge);
                // edge.n1.removeEdge(edge);
                edgeRemove.push(edge);
                this.graph.edges.splice(this.graph.edges.indexOf(edge), 1);
            }
        }

        edgeRemove.forEach((e) => {
            e.n0.removeEdge(e);
            e.n1.removeEdge(e);
        });

        this.updateID();
    }
    // add a node by edge intersection
    public edgeSplitByEdge(edge: Edge, updateID = true) {
        const nodes: Node[] = [];

        for (let i = this.graph.edges.length - 1; i > -1; --i) {
            const other = this.graph.edges[i];
            if (edge.n0 !== other.n0 &&
                edge.n0 !== other.n1 &&
                edge.n1 !== other.n0 &&
                edge.n1 !== other.n1) {
                const inter = getIntersectEdge(edge, other);
                if (inter) {
                    // console.log(inter)
                    const nn = new Node(this.graph, inter[0], inter[1])
                    this.graph.nodes.push(nn);
                    this.addEdgeSplit(nn, other.n0);
                    this.addEdgeSplit(nn, other.n1);

                    this.removeEdge(other);
                    nodes.push(nn);
                }
            }
        }

        if (nodes.length > 0) {
            let nodeFirst = edge.n0;
            nodes.push(edge.n1);
            const disList = nodes.map((n) => { return distance(n.vec, edge.n0.vec); });
            const indices = disList.map((val, index) => index);
            const sortedDis = indices.sort((a, b) => disList[a] - disList[b]);
            this.addEdgeSplit(nodeFirst, nodes[sortedDis[0]]);
            for (let i = 0; i < sortedDis.length - 1; ++i) {
                this.addEdgeSplit(nodes[sortedDis[i]], nodes[sortedDis[i + 1]]);
            }
            this.removeEdge(edge);
        }

        updateID && this.updateID();
    }
    // ..............................................................
    public removeNode(node: Node) {
        node.edges.forEach((edge) => {
            if (edge.n0 === node) {
                edge.n1.removeEdge(edge)
            } else {
                edge.n0.removeEdge(edge)
            }
            this.graph.edges.splice(this.graph.edges.indexOf(edge), 1);
        });

        this.graph.nodes.splice(this.graph.nodes.indexOf(node), 1);

        this.updateID();
        this.resetCapture();
    }
    public removeEdge(other: Edge) {
        other.n0.removeEdge(other);
        other.n1.removeEdge(other);
        this.graph.edges.splice(this.graph.edges.indexOf(other), 1);
    }
    // ..............................................................
    public findNode(node: Node) {
        let theDis = Number.MAX_VALUE;
        let index = -1;
        for (let i = 0; i < this.graph.nodes.length; ++i) {
            if (this.graph.nodes[i] === node) continue;
            const vec = this.graph.nodes[i].vec;
            const dis = distance(vec, node.vec);
            if (theDis > dis) {
                theDis = dis;
                index = i;
            }
        }
        if (index > -1) { return this.graph.nodes[index]; }
        return undefined;
    }
    public findNodeVec(x: number, y: number, minDis = 8) {
        let theDis = Number.MAX_VALUE;
        let index = -1;
        const v = new Vector3(x, y);
        for (let i = 0; i < this.graph.nodes.length; ++i) {
            const vec = this.graph.nodes[i].vec;
            const dis = distance(vec, v);
            if (theDis > dis) {
                theDis = dis;
                index = i;
            }
        }
        if (index > -1 && theDis < minDis) { return this.graph.nodes[index]; }
        return undefined;
    }
    public findEdgeVec(x: number, y: number, minDis = 8) {
        const mv = new Vector3(x, y, 0)
        for (let i = 0; i < this.graph.edges.length; ++i) {
            const edge = this.graph.edges[i];
            const cp = pointOnLine(mv, edge.n0.vec, edge.n1.vec);

            const dis = distance(mv, cp);
            if (dis < minDis) {
                return { edge, vec: cp };
            }
        }
        return undefined;
    }
    public findEdgeByEdge(edge: Edge) {
        return this.findEdgeByNodes(edge.n0, edge.n1);
    }
    public findEdgeByNodes(n0: Node, n1: Node) {
        for (let i = 0; i < this.graph.edges.length; ++i) {
            const other = this.graph.edges[i];
            if ((n0 === other.n0 && n1 === other.n1) || (n0 === other.n1 && n1 === other.n0)) {
                return other;
            }
        }
        return undefined;
    }
    public getEdgeByTwoNode(n0: Node, n1: Node) {
        for (let i = 0; i < this.graph.edges.length; ++i) {
            const edge = this.graph.edges[i];
            if ((edge.n0 === n0 && edge.n1 === n1) || (edge.n0 === n1 && edge.n1 === n0)) {
                return edge;
            }
        }
        return undefined;
    }
    public mergeTwoNodes(n0: Node, n1: Node) {

        const others = []
        for (let i = 0; i < n1.edges.length; ++i) {
            const edge = n1.edges[i];
            if (edge.n0 === n1) {
                others.push(edge.n1);
            } else {
                others.push(edge.n0);
            }
        }
        n1.remove();

        others.forEach((n) => {
            if (!this.getEdgeByTwoNode(n0, n)) {
                this.addEdge(n0, n);
            }
        })
    }
    // ..............................................................
    public updateID() {
        for (let i = 0; i < this.graph.nodes.length; ++i) { this.graph.nodes[i].id = i; }
        for (let i = 0; i < this.graph.edges.length; ++i) { this.graph.edges[i].id = i; }
    }
    public resetVisit() {
        for (let i = 0; i < this.graph.nodes.length; ++i) { this.graph.nodes[i].visited = false; }
    }

    public setNodeStart() {
        if (this.captured) {
            this.nodeStart = this.captured as Node;
        }
    }
    public setNodeEnd() {
        if (this.captured) {
            this.nodeEnd = this.captured as Node;
        }
    }
    // ..............................................................
    public computePath() {
        if (this.nodeStart && this.nodeEnd) {
            return computePath(this.graph, this.nodeStart.id, this.nodeEnd.id);
        }
    }
    public computePathAStar() {
        if (this.nodeStart && this.nodeEnd) {
            return computePathAStar(this.graph, this.nodeStart.id, this.nodeEnd.id);
        }
    }
    // ..............................................................
    public computeMST() {
        const mst = computeMST(this.graph);
        this.pathMST = mst;
        return mst;
    }
    public computeBFS() {
        this.resetVisit();
        const arraySearch = getBFS(this.graph.nodes[0]);
        const ids = arraySearch.map((d) => d.id);
        // console.log(ids);
        return ids;
    }
    public computeDFS() {
        this.resetVisit();
        const arraySearch = getDFS(this.graph.nodes[0]);
        const ids = arraySearch.map((d) => d.id);
        // console.log(ids);
        return ids;
    }
}

// ..............................................................

const getIntersectEdge = (e0: Edge, e1: Edge) => {
    return getIntersect(e0.n0, e0.n1, e1.n0, e1.n1);
};
const getIntersect = (n0: Node, n1: Node, n2: Node, n3: Node) => {
    return getLineIntersectPlane(n0.vec.x, n0.vec.y, n1.vec.x, n1.vec.y, n2.vec.x, n2.vec.y, n3.vec.x, n3.vec.y);
};
const computeMST = (graph) => {
    const pathMST = getMST(graph);
    return pathMST;
};
const computePath = (graph, idStart, idEnd) => {
    const path = getPath(graph, graph.nodes[idStart], graph.nodes[idEnd]);
    return path;
};
const computePathAStar = (graph, idStart, idEnd) => {
    const path = getPathAStar(graph, graph.nodes[idStart], graph.nodes[idEnd]);
    return path;
};
