import { Node, Graph } from '../Graph';

export const getPath = (graph: Graph, p0: Node, p1: Node) => {
    propagationByNode(graph.nodes, p0, costFnDistance, 0.1);
    return backPropagation(graph.nodes, p1);
}


const distanceTo = (x0: number, y0: number, z0: number, x1: number, y1: number, z1: number): number => {
    return Math.sqrt((x0 - x1) * (x0 - x1) + (y0 - y1) * (y0 - y1) + (z0 - z1) * (z0 - z1));
}
// ..............................................................
const costFnDistance = (p0: Node, p1: Node, beta: number = 0.0002) => {
    return distanceTo(p0.vec.x, p0.vec.y, p0.vec.z, p1.vec.x, p1.vec.y, p1.vec.z);
}
const propagationByNode = (nodes: Node[], origin: Node, optimizer?: (current: Node, np: Node, Beta: number) => number | undefined, Beta: number = 1) => { //, PixelMap pMap, transitionCostDelegate cost) {
    if (optimizer === undefined) {
        optimizer = costFnDistance;
    }

    for (let i = 0; i < nodes.length; ++i) { nodes[i].reset(); }

    let queue: Node[] = [origin];
    origin.networkDistance = 0;

    for (let i = 0; i < 10000000; ++i) {
        if (queue.length === 0) { break; }
        let currentNode: Node = queue[0];
        queue.shift();

        for (let j = 0; j < currentNode.edges.length; ++j) {
            const edge = currentNode.edges[j];
            const np: Node = currentNode === edge.n0 ? edge.n1 : edge.n0

            const newCost = optimizer(currentNode, np, Beta);
            const result = (currentNode.networkDistance + newCost);
            const newTemp = result; /// this.UNIT_VALUE; // result < 0.001 ? result  : result / this.UNIT_VALUE;
            if (np.networkDistance === -1.0 || newTemp < np.networkDistance) {
                np.networkDistance = newTemp;
                queue.push(np);
            }
        }
    }
}

const backPropagation = (nodes: Node[], destination: Node): Node[] => {
    let j = 0; let k = 0;
    // this.nodeTarget = destination;

    let path: Node[] = [];
    if (destination.networkDistance !== -1.0) {
        path.push(nodes[destination.id]);

        let currentNode: Node = destination;
        for (j = 0; j < 100000; ++j) {

            let smallest: Node = currentNode;
            smallest.countVisit++;

            for (k = 0; k < currentNode.edges.length; ++k) {
                const edge = currentNode.edges[k];
                const np: Node = currentNode === edge.n0 ? edge.n1 : edge.n0;

                if (np.networkDistance < smallest.networkDistance) {
                    smallest = np;
                }
            }

            if (smallest === currentNode) {
                break;
            }
            currentNode = smallest;
            path.push(nodes[currentNode.id]);

        }

    }
    return path;
}


class GraphDijkstra {
    public nodes: NodeDijkstra[] = [];
    public edges: EdgeDijkstra[] = [];
    public nodeOrigin: Node;
    public nodeTarget: Node;
    public constructor() {

    }
}
class EdgeDijkstra {
    public id: number = -1;
    public n0: NodeDijkstra;
    public n1: NodeDijkstra;

    constructor(n0: NodeDijkstra, n1: NodeDijkstra) {
        this.n0 = n0;
        this.n1 = n1;
        this.n0.edges.push(this);
        this.n1.edges.push(this);
        n0.nodes.push(n1);
        n1.nodes.push(n0);
    }
}
class NodeDijkstra {
    public id: number = -1;
    public x: number;
    public y: number;
    public z: number;

    public edges: EdgeDijkstra[] = [];
    public nodes: NodeDijkstra[] = [];

    public networkDistance: number = -1;
    public countVisit: number = 0;

    constructor(x: number, y: number, z: number) {
        this.x = x;
        this.y = y;
        this.z = z;
    }
    public reset(): void {
        this.networkDistance = -1;
        this.countVisit = 0;
    }
    public deepCopy(): NodeDijkstra {
        let n = new NodeDijkstra(this.x, this.y, this.z);
        n.id = this.id;
        return n;
    }
}


