import { Controller } from '..';
import { Node, Edge } from '../../GraphSystem';
import { getDelaunay } from '../../GraphSystem/Algorithm/Delaunay';

export class GraphRenderer {
    public controller: Controller;

    public toggleDelaunay = false;
    public toggleMST = false;

    public toggleDijkstra = false;
    public toggleAStar = false;
    
    constructor(controller: Controller) {
        this.controller = controller;
    }
    public render(ctx: CanvasRenderingContext2D) {

        // this.graph.render(ctx);
        // this.divSel.textContent = JSON.stringify(this.graphSystem.captured);

        if (this.controller.graphSystem.captured) {
            this.controller.divSel.textContent = this.controller.graphSystem.captured.type;
        } else {
            this.controller.divSel.textContent = 'undefined';
        }

        this.renderNodeStartEnd(ctx);

        ctx.beginPath();
        ctx.arc(this.controller.m.x, this.controller.m.y, 2, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fillStyle = '#888';
        ctx.fill();


        if (this.toggleMST) {
            if (this.controller.graphSystem.pathMST) {
                const nodes = this.controller.graphSystem.graph.nodes
                this.controller.graphSystem.pathMST.forEach( e => {
                    const v0 = nodes[e.n0].vec;
                    const v1 = nodes[e.n1].vec;
                    ctx.beginPath();
                    ctx.moveTo(v0.x, v0.y);
                    ctx.lineTo(v1.x, v1.y);
                    ctx.lineWidth = 5;
                    ctx.stroke();
                });
            }
        }

        if (this.toggleDelaunay) {
            getDelaunay(ctx, this.controller.graphSystem.graph.nodes);
        }

        if (this.toggleDijkstra  && this.controller.graphSystem.nodeStart && this.controller.graphSystem.nodeEnd) {
            const paths = this.controller.graphSystem.computePath();
            ctx.beginPath();
            ctx.moveTo(paths[0].vec.x, paths[0].vec.y);
            for(let i = 1 ; i < paths.length; ++i) ctx.lineTo(paths[i].vec.x, paths[i].vec.y);
            ctx.strokeStyle = '#f0f';
            ctx.lineWidth = 5;
            ctx.stroke();
        }

        if (this.toggleAStar  && this.controller.graphSystem.nodeStart && this.controller.graphSystem.nodeEnd) {
            const paths = this.controller.graphSystem.computePathAStar();
            ctx.beginPath();
            ctx.moveTo(paths[0].x, paths[0].y);
            for(let i = 1 ; i < paths.length; ++i) ctx.lineTo(paths[i].x, paths[i].y);
            ctx.strokeStyle = '#f0f';
            ctx.lineWidth = 5;
            ctx.stroke();
        }

        this.renderGraph(ctx);

    }
    public renderNodeStartEnd(ctx) {
        ctx.fillStyle = '#0ff';
        if (this.controller.graphSystem.nodeStart) {
            const v = this.controller.graphSystem.nodeStart.vec;
            ctx.beginPath();
            ctx.arc(v.x, v.y, 8, 0, Math.PI * 2.0);
            ctx.closePath();
            ctx.fill();
        }
        if (this.controller.graphSystem.nodeEnd) {
            const v = this.controller.graphSystem.nodeEnd.vec;
            ctx.beginPath();
            ctx.arc(v.x, v.y, 8, 0, Math.PI * 2.0);
            ctx.closePath();
            ctx.fill();
        }
    }
    public renderGraph(ctx: CanvasRenderingContext2D) {
        this.controller.log = `N:${this.controller.graphSystem.graph.nodes.length}, E:${this.controller.graphSystem.graph.edges.length}`;
        // console.log(`N:${this.controller.graphSystem.graph.nodes.length}, E:${this.controller.graphSystem.graph.edges.length}`)
        this.controller.graphSystem.graph.edges.forEach((e: Edge) => { this.renderGraphEdge(ctx, e); })
        this.controller.graphSystem.graph.nodes.forEach((n: Node) => { this.renderGraphNode(ctx, n); })
    }
    public renderGraphNode(ctx: CanvasRenderingContext2D, node: Node) {

        ctx.beginPath();
        ctx.arc(node.vec.x, node.vec.y, node.r, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fillStyle = '#ff0000';
        ctx.fill();
        if (node.isSelected) {
            ctx.lineWidth = 3;
            ctx.strokeStyle = '#00f';
            ctx.stroke();
        }

        ctx.strokeStyle = '#ff0000';
        ctx.lineWidth = 1;
        ctx.strokeText(`${node.networkDistance}`, node.vec.x, node.vec.y - 20);
        ctx.strokeText(`${node.id}`, node.vec.x, node.vec.y - 7);
    }
    public renderGraphEdge(ctx: CanvasRenderingContext2D, edge: Edge) {
        if (edge.isSelected) {
            ctx.lineWidth = 3;
            ctx.strokeStyle = '#00f';
            ctx.beginPath();
            ctx.moveTo(edge.n0.vec.x, edge.n0.vec.y);
            ctx.lineTo(edge.n1.vec.x, edge.n1.vec.y);
            ctx.stroke();
        }
        ctx.lineWidth = 1;
        ctx.beginPath();
        ctx.moveTo(edge.n0.vec.x, edge.n0.vec.y);
        ctx.lineTo(edge.n1.vec.x, edge.n1.vec.y);
        ctx.strokeStyle = '#0f0';
        ctx.stroke();

        ctx.strokeStyle = '#00ff00';
        ctx.lineWidth = 1
        const x = edge.n0.vec.x + (edge.n1.vec.x - edge.n0.vec.x) * 0.5
        const y = edge.n0.vec.y + (edge.n1.vec.y - edge.n0.vec.y) * 0.5
        ctx.strokeText(`${edge.id}`, x, y - 7)
    }
}