import { RendererCanvas } from '../../../../lib';

import { Controller } from './Controller';
import { UIGraph } from './UIGraph';

export class Solution extends RendererCanvas {
    public controller: Controller;
    public ui: UIGraph;

    constructor(id: string) {
        super(document.getElementById(id) as HTMLDivElement);
        this.canvas.style.position = 'absolute';

        this.init();
    }
    public init() {
        this.controller = new Controller(this.canvas);
        this.mouseClick(300, 300);

        this.ui = new UIGraph(this);
        this.start()
    }

    public render(ctx: CanvasRenderingContext2D): void {
        this.controller.render(ctx);
    }
    public mouseClick(x: number, y: number): void {
        this.controller.mouseClick(x, y);
    }
    public mouseDown(x: number, y: number): void {
        this.controller.mouseDown(x, y)
    }
    public mouseMove(x: number, y: number): void {
        this.controller.mouseMove(x, y);
    }
    public mouseDrag(x: number, y: number): void {
        this.controller.mouseDrag(x, y)
    }
    public mouseUp() {
        this.controller.mouseUp();
    }
}


