
import { Vector3, distance } from './util';

import { GraphSystem} from '..';

export class GraphCommon {
    public type = 'node';
    public isSelected = false;
    public id = 0;
    public visited = false;
    public data: any = [];
}
export class Graph {
    public graphSystem: GraphSystem;
    public nodes: Node[] = [];
    public edges: Edge[] = [];

    constructor(graphSystem: GraphSystem) {
        this.graphSystem = graphSystem;
    }
   
}
export class Edge extends GraphCommon {
    public n0: Node;
    public n1: Node;
    public length: number = -1;
    constructor(n0: Node, n1: Node) {
        super();
        this.type = 'edge';
        this.n0 = n0;
        this.n1 = n1;
    }
    public getLength() {
        this.length = distance(this.n0.vec, this.n1.vec);
        return this.length;
    }
    public toJSON() {
        return {
            id: this.id,
            n0: this.n0.id,
            n1: this.n1.id,
            data: this.data,
        };
    }
    public remove() {
        this.n0.removeEdge(this);
        this.n1.removeEdge(this);
    }
}
export class Node extends GraphCommon {
    public graph: Graph;
    public vec: Vector3;
    public edges: Edge[] = [];
    public r: number = 5;

    public networkDistance = -1;
    public countVisit = 0;
    
    constructor(graph: Graph, x: number, y: number, z: number = 0) {
        super();
        this.graph = graph;
        this.type = 'node';
        this.vec = new Vector3(x, y, z);
    }
    public reset(): void {
        this.networkDistance = -1;
        this.countVisit = 0;
        this.visited = false;
    }
    public toJSON() {
        return {
            id: this.id,
            vec: {x: this.vec.x, y: this.vec.y, z: this.vec.z},
            data: this.data
        };
    }
    public remove() {
        this.graph.graphSystem.removeNode(this);
    }
    public removeEdge(other: Edge) {
        this.edges.splice(this.edges.indexOf(other), 1);
    }
}
