import { Node } from '../Graph';

// ........................................... BFS
export const getBFS = (node: Node) => {
    let num = 0;
    let outSortedBFS = [];
    let queue: Node[] = [];
    node.visited = true;

    queue.push(node);
    outSortedBFS.push(node);

    while (queue.length > 0) {
        const current = queue.shift();

        for (let i = 0; i < current.edges.length; ++i) {
            const edge = current.edges[i];
            const other = edge.n0 === current ? edge.n1 : edge.n0;
            if (!other.visited) {
                other.visited = true;
                outSortedBFS.push(other);
                queue.push(other);
            }
        }
        if (num++ > 100000) {
            // console.log('safeguard'); 
            return;
        }
    }
    return outSortedBFS;
};

export const getDFS = (node: Node) => {
    const outSortedDFS = [];
    const dfs = (node: Node) => {
        if (node.visited) { return; }
        node.visited = true;
        outSortedDFS.push(node);
        for (let i = 0; i < node.edges.length; ++i) {
            const edge = node.edges[i];
            const other = edge.n0 === node ? edge.n1 : edge.n0;
            dfs(other);
        }
    }
    dfs(node);
    return outSortedDFS;
};