
export class SceneManager {
  private histories: any = [];
  private currentIndex = -1;
  public reset() {
    this.histories = [];
    this.currentIndex = -1;
  }
  public push(data: any) {
    if (this.currentIndex < this.histories.length - 1) {
      this.histories.splice(this.currentIndex + 1, this.histories.length);
    }
    this.histories.push(data);
    this.currentIndex = this.histories.length - 1;
  }
  public getCurrentIndex() {
    return this.currentIndex;
  }
  public getHistoryLength() {
    return this.histories.length;
  }
  public getCurrentState() {
    return { currentIndex: this.getCurrentIndex(), historyNum: this.getHistoryLength() };
  }
  public undo() {
    if (this.currentIndex === 0) { return undefined; }
    this.currentIndex--;
    return this.histories[this.currentIndex];
  }
  public redo() {
    this.currentIndex++;
    this.currentIndex = this.currentIndex < this.histories.length ? this.currentIndex : this.histories.length - 1;
    return this.histories[this.currentIndex];
  }
}

export class SceneState {
  private manager: SceneManager;
  constructor() {
    // this.scene = scene;
    this.manager = new SceneManager();
  }
  public getLog() {
    return this.manager.getCurrentState();
  }
  public resetState() {
    this.manager.reset();
    this.captureState([]);
  }
  public captureState(data) {
    this.manager.push(data);
    // this.manager.push(this.scene.common.getData());
    const state = this.manager.getCurrentState();
    // console.log(state);
    return state;
  }
  public undo() {
    return this.manager.undo();
    // const state = this.manager.undo();
    // this.scene.common.populateArea(state);
  }
  public redo() {
    return this.manager.redo();
    // const state = this.manager.redo();
    // this.scene.common.populateArea(state);
  }
}


