import { Graph } from '../Graph'
class GraphMST {
    private nodes = [];
    private edges = [];
    constructor() {
        this.nodes = [];
        this.edges = [];
    }
    addVertex(node) {
        this.nodes.push(node);
    }

    addEdge(n0, n1, weight) {
        this.edges.push(
            { n0, n1, weight }
        );
    }
    primMST(startNode) {
        const visited = {};
        const MST = [];

        visited[startNode] = true;

        while (MST.length < this.nodes.length - 1) {
            let minEdge = null;

            this.edges.forEach(edge => {
                if ((visited[edge.n0] && !visited[edge.n1]) || (!visited[edge.n0] && visited[edge.n1])) {
                    if (!minEdge || edge.weight < minEdge.weight) {
                        minEdge = edge;
                    }
                }
            });

            if (!minEdge) break;

            MST.push(minEdge);
            visited[minEdge.n0] = true;
            visited[minEdge.n1] = true;
        }
        return MST;
    }
}


export const getMST = (graph: Graph) => {
    const g = new GraphMST();

    for (let i = 0; i < graph.nodes.length; ++i) {
        g.addVertex(graph.nodes[i].id);
    }
    for (let i = 0; i < graph.edges.length; ++i) {
        const edge = graph.edges[i];
        g.addEdge(edge.n0.id, edge.n1.id, edge.getLength() )
    }

    const MST = g.primMST(0);
    // console.log("Minimum Spanning Tree:");
    // MST.forEach(edge => { console.log(`${edge.n0} - ${edge.n1} : ${edge.weight}`); });

    return MST;
}