import { downloadJson } from '../../GraphSystem/Graph/util';
import { Controller } from '..';
import { GraphSystem } from '../../GraphSystem';
import { SceneState } from './SceneManager';

export class Common {
    public controller: Controller;
    public sceneState: SceneState;

    public graphSystem: GraphSystem;

    constructor(controller: Controller) {
        this.controller = controller;
        this.graphSystem = this.controller.graphSystem;
        this.sceneState = new SceneState();
    }
    public reset() {
        const graph = this.controller.graphSystem.graph;
        graph.nodes = [];
        graph.edges = [];
        this.capture();
    }
    public capture() {
        // console.trace();
        this.sceneState.captureState(this.toJSON());
        this.updateUndoRedoHist();
    }
    public updateUndoRedoHist() {
        if (this.controller.divHist) {
            const hist = this.sceneState.getLog();
            this.controller.divHist.textContent = `hist: ${hist.currentIndex}/${hist.historyNum-1}`;
        }
    }
    // ...........................................
    public undo() {
        const data = this.sceneState.undo();
        data && this.dump(data);
        this.updateUndoRedoHist();
        this.controller.graphSystem.resetCapture();
        // console.log('undo', data);
    }
    public redo() {
        const data = this.sceneState.redo();
        data && this.dump(data);
        this.updateUndoRedoHist();
        this.controller.graphSystem.resetCapture();
        // console.log('redo', data);
    }
    // ...........................................
    public toJSON() {
        const data = { node: [], edge: [] }
        this.controller.graphSystem.graph.nodes.forEach((n) => { data.node.push(n.toJSON()); });
        this.controller.graphSystem.graph.edges.forEach((e) => { data.edge.push(e.toJSON()); });
        return data;
    }
    public exportJSON() {
        const obj = JSON.stringify(this.toJSON());
        downloadJson(obj, 'graph.json');
    }
    public dump(data) {
        const graphSystem = this.controller.graphSystem;
        graphSystem.graph.nodes = [];
        graphSystem.graph.edges = [];

        for (let i = 0; i < data.node.length; ++i) {
            const nodeData = data.node[i];
            const node = graphSystem.addNode(nodeData.vec.x, nodeData.vec.y, nodeData.vec.z);
            node.id = nodeData.id;
            node.data = nodeData.data;
        }
        for (let i = 0; i < data.edge.length; ++i) {
            const edgeData = data.edge[i];
            const edge = graphSystem.addEdge(graphSystem.graph.nodes[edgeData.n0], graphSystem.graph.nodes[edgeData.n1]);
            edge.id = edgeData.id;
            edge.data = edgeData.data;
        }
    }
    public populate() {
        const x = Math.random() * this.controller.canvas.width;
        const y = Math.random() * this.controller.canvas.height;
        this.controller.mouseClick(x, y);
    }
    // ...........................................
    public toggleDelaunay(v: boolean) { this.controller.renderer.toggleDelaunay = v; }
    public toggleMST(v: boolean) { 
        this.controller.graphSystem.computeMST();
        this.controller.renderer.toggleMST = v; 
    }
    public computedBSF() {
        const result = this.controller.graphSystem.computeBFS();
        this.updateSearchResult('BFS: '+ result.join(', '));
    }
    public computedDFS() {
        const result = this.controller.graphSystem.computeDFS();
        this.updateSearchResult('DFS: '+ result.join(', '));
    }
    public updateSearchResult(result: string) {
        this.controller.divSearchResult.textContent = result;
    }
    public computedDijkstra(v: boolean) {
        this.controller.renderer.toggleDijkstra = v;
    }
    public computedAStar(v: boolean) {
        this.controller.renderer.toggleAStar = v;
    }
    // ...........................................
    public removeSelection() {
        // console.log(this.graphSystem.captured)
        if (this.controller.graphSystem.captured) {
            this.controller.graphSystem.captured.remove();
            this.controller.graphSystem.captured = undefined;
        }
    }
    public addNodeWithClosestNode(x,y) {
        const node = this.graphSystem.addNode(x, y, 0);
        this.graphSystem.addEdgeByClosestNode(node);
    }
}